
.displayStyle{
  display: inline-block;
  margin: 20px;
  border: 3px solid #383838;
  padding: 15px;
  border-radius: 12px;
  background-color: bisque;
}
.displayStyle:hover{
  background-color: #fccb90;
  border: 3px solid tomato;
}
.imgStyle{
  width: 300px;
  height: 200px;
  border-radius: 7px;
}